import React from 'react';
import './HeaderCom.css'
import {NavLink} from 'react-router-dom'

let HeaderCom = (props) => {
    let path = props.location.pathname.split("/")[1];
    let header;
    switch(path){
        case 'product':
            header="商品"
            break;
        case 'timepro':
            header="限时抢购"
            break;
        case 'detail':
            header="商品详情"
            break;
        case 'user':
            header="个人中心"
            break;
        case 'shopcar':
            header="购物车"
            break;
        default:
            header=""
    }
    return <div className="back_bg">
                <div className="backhome" onClick={()=>{window.history.go(-1)}}><a><img src="/image/back.png"/></a></div>
                <div className="pos">{header}</div> 
                <div className="n_arrow"><a id="nav"><img src="/image/nav.png"/></a></div>      
                <div className="nav_con">     
                    <ul>
                        <li className="b_1 cur"><NavLink to="/home"><p></p>首页</NavLink></li>
                        <li className="b_2"><NavLink to="/product"><p></p>分类</NavLink></li>
                        <li className="b_3"><NavLink to="/shopcar"><p></p>购物车</NavLink></li>
                        <li className="b_4"><NavLink to="/user"><p></p>会员中心</NavLink></li>
                    </ul>
                </div>              
            </div> 
}

export default HeaderCom

 