import React from 'react';
import './UserInfo.css'
import {connect} from 'react-redux'
import * as types from '../store/type'
import {asyncActionEixt} from '../store/anyscAction'

let UserInfo = (props) => {
    let {user} = props;
    return <div className="user_bg">
                <div className="user_t">
                    <table>
                    <tbody><tr valign="top">
                        <td rowSpan="2" width="118">
                            <div className="u_header_bg">
                                <div className="u_header"><img src={user.userHead}/></div>
                            </div>
                        </td>
                        <td>
                            <span className="fl">{user.userName}</span>
                            <div className="acc_btn" onClick={()=>{props.logout()}}><a>{user.msg}</a></div>
                        </td>
                    </tr>
                    <tr valign="top">
                        <td>
                            等级：{user.userGrade} <br/>
                            您还差 270 积分达到 分红100
                        </td>
                    </tr>
                    </tbody></table>
                </div>
                <div className="user_b">
                    <table>
                    <tbody><tr>
                        <td width="25%">账户余额</td>
                        <td width="25%">消费金额</td>
                        <td width="25%">返还积分</td>
                        <td width="25%">红包个数</td>                                    
                    </tr>
                    <tr>
                        <td>￥200元</td>
                        <td>￥200元</td>
                        <td>98R</td>
                        <td>3个</td>
                    </tr>
                    </tbody></table>
                </div>
            </div>
}

let MapStateToProps = (state)=>({
    // list:state.list
})

let MapDispatchToProps = (dispatch)=>({
    logout:()=>{asyncActionEixt(
        dispatch
    )}
})

export default connect(
    MapStateToProps,
    MapDispatchToProps
)(UserInfo)