import * as types from './type';

export const asyncActionPro = (opt,dispatch,type)=>{
    dispatch({type:types.VIEW_LOADING,payload:true})
    fetch(
        'http://localhost:3000/index',{
            method:'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body:JSON.stringify(opt)
        }
    ).then(
        res=>res.json()
    ).then(
        data=>{
            dispatch({type:types.VIEW_LOADING,payload:false})
            dispatch({type:type,payload:data})
        }
    )
}

export const asyncActionDetail = (opt,dispatch,type,index)=>{
    fetch(
        'http://localhost:3000/index',{
            method:'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body:JSON.stringify(opt)
        }
    ).then(
        res=>res.json()
    ).then(
        data=>{
            dispatch({type:types.VIEW_LOADING,payload:false})
            let detail;
            for(var i=0;i<data.length;i++){
                if (data[i].goodId==index) {
                    detail = data[i]
                }
            }
            dispatch({type:type,payload:detail})
        }
    )
    return {type:types.VIEW_LOADING,payload:true}
}

export let asyncActionAuth = (opt,type) => (dispatch,getState)=>{
    dispatch({type:types.VIEW_LOADING,payload:true});
    fetch(
        'http://localhost:3000/login',{
            credentials:"include",
            method:'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body:JSON.stringify(opt)
        }
    ).then(
      res=>res.json()
    ).then(
      data=>{
          if(data==0){
                alert("登录失败")
                dispatch({type:types.VIEW_LOADING,payload:false});
            }else{
                dispatch({type:type,payload:data[0]})
                dispatch({type:types.VIEW_LOADING,payload:false});
          }
      }
    );
  };

