import React from 'react';
import './Header.css';
import {NavLink} from 'react-router-dom'

let Header = () => (
    <div className="header">
        <div className="top">
            <div id="logo">
                <NavLink to="/home"><img src="/image/logo.png"/></NavLink>
            </div>
            <div className="search">
                <form>
                    <input type="text" defaultValue="" className="s_ipt"/>
                    <input type="button" defaultValue="" className="s_btn"/>
                </form>
            </div>        
            <div className="t_log"><NavLink to="/login">登录</NavLink></div> 
        </div>
    </div>
)

export default Header