import React from 'react'
import './LogHeader.css'
import {NavLink} from 'react-router-dom'

let LogHeader = (props) => {
    let {data} = props
    return <div className="back_bg">
        <table>
            <tbody>
                <tr>
                    <td><NavLink to="/home">取消</NavLink></td>
                    <td>{data.name1}</td>
                    <td><NavLink to={data.path}>{data.name}</NavLink></td>
                </tr>
            </tbody>
        </table>        
    </div>  
}

export default LogHeader



