import React from 'react';
import LogHeader from '../common/LogHeader';
import LogContent from './LogContent';
import QueueAnim from 'rc-queue-anim';

let Login = (props) => (
    <QueueAnim animConfig={{opacity:[1, 0]}} duration="3000">
    <div key={1} className="login">
        <LogHeader data={{name:"注册",name1:"登录",path:"/reg"}}></LogHeader>
        <LogContent {...props}></LogContent>
    </div>
    </QueueAnim>
)

export default Login