import React from 'react';
import {connect} from 'react-redux'
import {Route,Redirect,Switch} from 'react-router-dom'


import Header from '../common/Header';
import HeaderCom from '../common/HeaderCom';
import ErrorMsg from '../common/Error';
import Footer from '../common/Footer';
import Footer1 from '../common/Footer1';
// import Home from './Home';
import User from './User';
import Login from './Login';
// import Reg from './Reg';
// import TypeList from './TypeList';
// import Detail from './Detail';
// import ShopCar from './ShopCar';
// import TimePro from './TimePro';
import Loading from '../common/Loading'


import loadable from '../api/loadable'
import * as types from '../store/type'
import AuthUser from '../guard/AuthUser';
import {asyncActionAuto} from "../store/anyscAction"

let Home = loadable(()=>import('./Home'))
let ShopCar = loadable(()=>import('./ShopCar'))
let TimePro = loadable(()=>import('./TimePro'))
let Reg = loadable(()=>import('./Reg'))
let TypeList = loadable(()=>import('./TypeList'))
let Detail = loadable(()=>import('./Detail'))

class App extends React.Component{
    constructor(props){
        super()
        judge(props.location.pathname,props)
    }
    componentDidMount(){
        this.props.auto()
    }
    componentWillReceiveProps(nextProps){
        if(this.props.location.pathname !== nextProps.location.pathname){
            let routerPath = nextProps.location.pathname;
            window.scrollTo(0,0)
            judge(routerPath,this.props)
        }
    }
    render(){
        return (
            <>  
                {this.props.bLoading && <Loading/>}
                {this.props.bHeader1 && <Header/>}
                {this.props.bHeader2 && <HeaderCom {...this.props}/>}
                <Switch>
                <Route path="/home" component={Home}/>
                <AuthUser path="/user" component={User}/>
                <Route path="/login" component={Login}/>
                <Route path="/reg" component={Reg}/>
                <Route path="/product" component={TypeList}/>
                <Route path="/detail/:goodId" component={Detail}/>
                <AuthUser path="/shopcar" component={ShopCar}/>
                <Route path="/timepro" component={TimePro}/>
                <Redirect exact from="/" to="/home"/>
                <Route component={ErrorMsg}/>
                </Switch>
                {this.props.bFooter1 && <Footer/>}
                {this.props.bFooter2 && <Footer1/>}
            </>
        )
    }
    
}

function judge(routerPath,props){
    if(/home/.test(routerPath)){
        props.view_footer1(true);
        props.view_footer2(false);
        props.view_header1(true);
        props.view_header2(false);
    }
    if(/shopcar|user|product|timepro/.test(routerPath)){
        props.view_footer1(true);
        props.view_footer2(false);
        props.view_header1(false);
        props.view_header2(true);
    }
    if(/login|reg/.test(routerPath)){
        props.view_footer1(false);
        props.view_footer2(false);
        props.view_header1(false);
        props.view_header2(false);
    }
    if(/detail/.test(routerPath)){
        props.view_footer1(false);
        props.view_footer2(false);
        props.view_header1(false);
        props.view_header2(true);
    }
}
    
let MapStateToProps=(state)=>{
    return {
    bLoading:state.bLoading,
    bHeader1:state.bHeader1,
    bHeader2:state.bHeader2,
    bFooter1:state.bFooter1,
    bFooter2:state.bFooter2,
    bLoading:state.bLoading,
    user:state.user
}}

let MapDispatchToProps=(dispatch)=>({
    view_header1:(bl)=>{dispatch({type:types.VIEW_HEADER1,payload:bl})},
    view_header2:(bl)=>{dispatch({type:types.VIEW_HEADER2,payload:bl})},
    view_footer1:(bl)=>{dispatch({type:types.VIEW_FOOTER1,payload:bl})},
    view_footer2:(bl)=>{dispatch({type:types.VIEW_FOOTER2,payload:bl})},
    auto:()=>(dispatch(asyncActionAuto(types.UPDATE_USER)))
})


export default connect(
    MapStateToProps,
    MapDispatchToProps
)(App)
