import * as types from './type'

let reducer = (state,action)=>{
    let {type,payload}=action;
    switch(type){
        case types.VIEW_HEADER1:
            return Object.assign({},state,{
                bHeader1:payload
            })
        case types.VIEW_HEADER2:
            return Object.assign({},state,{
                bHeader2:payload
            })
        case types.VIEW_HEADER3:
            return Object.assign({},state,{
                bHeader3:payload
            })
        case types.VIEW_FOOTER1:
            return Object.assign({},state,{
                bFooter1:payload
            })
        case types.VIEW_FOOTER2:
            return Object.assign({},state,{
                bFooter2:payload
            })
        case types.VIEW_LOADING:
            return Object.assign({},state,{
                bLoading:payload
            })

        case types.UPDATE_LIST:
            return Object.assign({},state,{
                list:payload
            })
        case types.CLEAR_LIST:
            return Object.assign({},state,{
                list:[]
            })

        case types.UPDATE_HOTLIST:
            return Object.assign({},state,{
                hotList:payload
            })
        case types.CLEAR_HOTLIST:
            return Object.assign({},state,{
                hotList:[]
            })

        case types.UPDATE_HOTTOPLIST:
            return Object.assign({},state,{
                hotTopList:payload
            })
        case types.CLEAR_HOTTOPLIST:
            return Object.assign({},state,{
                hotTopList:[]
            })

        case types.UPDATE_TIMELIST:
            return Object.assign({},state,{
                timeList:payload
            })
        case types.CLEAR_TIMELIST:
            return Object.assign({},state,{
                timeList:[]
            })

        case types.UPDATE_DETAIL:
            return Object.assign({},state,{
                detail:payload
            })
        case types.CLEAR_DETAIL:
            return Object.assign({},state,{
                detail:[]
            })
        case types.UPDATE_USER:
        console.log(payload)
            return Object.assign({},state,{
                user:payload
            })
        case types.UPDATE_SHOPCAR:
            let arr = [...state.user.shopCar];
            let userData = {...state.user};
            let find = false;
            arr.forEach((item,index)=>{
            if(item.goodId==payload.goodId){
                item.number+=payload.number;
                find = true;
            }
            })
            if(!find){
                arr.push(payload)
            }
            userData.shopCar = arr
            return Object.assign({},state,{
                user:userData
            })
        case types.CLEAR_SHOPCAR:
            let userData2 = {...state.user};
            userData2.shopCar = []
            return Object.assign({},state,{
                user:userData2
            })
        case types.UPDATE_SHOPCAR_NUM:
            let arr1 = [...state.user.shopCar];
            let userData1 = {...state.user};
            if(payload.type=="1"){
                arr1.forEach((item,index)=>{
                    if(item.goodId==payload.id){
                        item.number++
                    }
                });
            }else if(payload.type=="0"){
                arr1.forEach((item,index)=>{
                    if(item.goodId==payload.id){
                        if(item.number<=1){
                            item.number=1
                        }else{
                            item.number--
                        }
                    }
                });
            }else if(payload.type=="del"){
                arr1.forEach((item,index)=>{
                    if(item.goodId==payload.id){
                        arr1.splice(index,1)
                    }
                });
            }
            userData1.shopCar = arr1
            return Object.assign({},state,{
                user:userData1
            })
        case types.CLEAR_USER:
            return Object.assign({},state,{
                user:payload
            })

        default:
            return state
    }
}
export default reducer