 import React from 'react';
import './LogContent.css';
import {asyncActionAuth} from '../store/anyscAction'
import {connect} from 'react-redux'
import * as types from '../store/type'

class LogContent extends React.Component{
	constructor(props){
		super();
		this.state = {
			iptUser:"",
			iptPass:""
		}
	}
	componentWillReceiveProps(nextProps){
		if(this.props.user!==nextProps.user){
			this.props.history.push("/user")
		}
	}
    render(){
	 return <div className="LogContent">
				<div className="l_logo"><img src="/image/l_logo.png"/></div>
				<div className="login_bg">
					<form>
						<div className="log_tab">	
							<div className="log_c">
								<table>
									<tbody>
										<tr>
											<td className="log_c_img"><img src="/image/l_1.png"/></td>
											<td>账号：</td>
											<td><input type="text" name="iptUser" value={this.state.iptUser} onChange={this.changeIpt.bind(this)} placeholder="请输入账号" className="l_ipt"/></td>
										</tr>
									</tbody>
								</table>
							</div>
							<div className="log_c">
								<table>
								<tbody>
									<tr>
										<td className="log_c_img"><img src="/image/l_2.png"/></td>
										<td>密码：</td>
										<td><input type="password" name="iptPass" value={this.state.iptPass} onChange={this.changeIpt.bind(this)}   placeholder="请输入密码" className="l_ipt"/></td>
									</tr>
								</tbody></table>
							</div>
						</div>   
						<table className="lastTab">
						<tbody><tr height="50">
							<td>
								<label className="r_rad"><input type="checkbox" defaultValue=""/></label><label className="r_txt">自动登录</label>
								<span className="fr"><a style={{color:'#4ca6e0', fontSize:'1.3em'}}>忘记密码？</a></span>
							</td>
						</tr>
						<tr height="60">
							<td><input type="button" onClick={this.sendMsg.bind(this)} defaultValue="登录" className="l_btn"/></td>
						</tr>
						</tbody></table>
					</form>
				</div>
			</div>
	}
	changeIpt(ev){
		this.setState({
			[ev.target.name]:ev.target.value
		})
	}
	sendMsg(){
		this.props.login({userName:this.state.iptUser,pass:this.state.iptPass})
	}
}

let MapStateToProps = (state)=>({
	user:state.user
});

let MapDispatchToProps=(dispatch)=>({
	login:(opt)=>(dispatch(asyncActionAuth(	
		opt,
	  	types.UPDATE_USER
	)))
  });
  
export default connect(
	MapStateToProps,
	MapDispatchToProps
)(LogContent);