import React from 'react';
import './Footer.css';
import {NavLink} from 'react-router-dom'


let Footer = (props) => (
        <div className="b_btm">
            <ul>
                <li className="b_1"><NavLink activeClassName="cur" to='/home'><p></p>首页</NavLink></li>
                <li className="b_2"><NavLink activeClassName="cur" to='/product'><p></p>分类</NavLink></li>
                <li className="b_3"><NavLink activeClassName="cur" to='/shopcar'><p></p>购物车</NavLink></li>
                <li className="b_4"><NavLink activeClassName="cur" to='/user'><p></p>个人中心</NavLink></li>
            </ul>
        </div>
)

export default Footer