import React,{Component} from 'react'
export default function loaddable(importRedB) {
  class BlueB extends Component {
    constructor(props) {
      super(props);

      this.state = {
        component: null
      };
    }

    async componentDidMount() {
      const { default: component } = await importRedB();

      this.setState({
        component: component
      });

    }

    render() {
      const C = this.state.component;
      return C ? <C {...this.props} /> : null;
    }
  }

  return BlueB;
}