import React from 'react';
import {connect} from 'react-redux'
import {Route,Redirect} from "react-router-dom"

let AuthUser = ({component:Component,user,...rest}) => {
  console.log(user)
  return <Route {...rest} render={(props)=>{
    // console.log(props)
    // return setTimeout(()=>{user.msg?<Component {...props} user={user}/>: <Redirect to="/login"/>},0)
   return user.msg?<Component {...props} user={user}/>: <Redirect to="/login"/>
  }}/>
}

let MapStateToProps=state=>({user:state.user})

  export default connect(
    MapStateToProps
  )(AuthUser);